* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/*  1rem = 16px, fz 62.5% = 1rem => 10px */

html {
  font-size: 62.5%;
}

.hamburger-menu {
  width: 3rem;
  height: 3rem;
  // background-color: salmon;
  position: fixed;
  top: 5rem;
  right: 5rem;
  z-index: 200;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  cursor: pointer;
  transition: right .7s;

  .change & {
    right: 33rem;
  }
   
  .change &:hover span {
    opacity: 1;
    visibility: visible;
  }

  span {
    position: absolute;
    left: 5rem;
    width: 10rem;
    height: 4rem;
    background-color: #e2b646;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-family: "Baloo Da 2", serif;
    font-size: 1.6rem;
    letter-spacing: .1rem;
    opacity: 0;
    visibility: hidden;
    transition: all .2s;

    &::before {
      content: '';
      position: absolute;
      border-left: 1rem solid transparent;
      border-right: 1rem solid #e2b646;
      border-bottom: 1rem solid transparent;
      border-top: 1rem solid transparent;
      top: 50%;
      left: -2rem;
      transform: translateY(-50%);
    }
  } /*  span */
 
  .line {
    width: 100%;
    height: 0.2rem;
    background-color: #fff;
    box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.2);

      .change & {
        width: 100%;
        height: .2rem;
        background-color: #fff;
        box-shadow: 0.1rem .2rem rgba(0,0,0,0.2);
      }

      .change & {
        background-color: rgba(0,0,0,0.8);
      }
  } /*  line */

} /*  hamburger-menu */

.change .line-1 {
  transform: rotate(45deg) translate(.3rem, .8rem);
}

.change .line-2 {
  opacity: 0;
  visibility: hidden;
}

.change .line-3 {
  transform: rotate(-45deg) translate(.3rem, -.8rem);
}
 

.header {
  width: 100%;
  height: 100vh;
  position: relative;
  perspective: 100rem;
  overflow: hidden;

  .img-wrapper {
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.8);
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      opacity: .5;
      animation: scale 25s;
      // animation-name: scale;
      // animation-duration: 25s;
    }
  }

  @keyframes scale {
    0% {
      transform: scale(1.3);
    }
    100% {
      transform: scale(1);
    }
  }

  .banner {
    position: absolute;
    top: 30%;
    left: 15%;

    h1 {
      font-family: 'Baloo Da 2', serif;
      font-size: 8rem;
      font-weight: 300;
      color: #fff;
      width: 50%;
      line-height: 9rem;
      letter-spacing: .2rem;
      text-shadow: 0 .3rem .5rem rgba(0,0,0,0.4);
      animation: moveBanner 1s .5s forwards;
    }

    p {
      font-family: 'Josefin Slab',serif;
      font-size: 4rem;
      color: #fff;
      width: 70%;
      letter-spacing: 0.1rem;
      margin-bottom: 3rem;
      text-shadow: 0 .3rem .5rem rgba(0,0,0,0.4);
      opacity: 0;
      animation: moveBanner 1s .7s forwards;
    }

    button {
      width: 25rem;
      height: 7rem;
      background-color: #c29525;
      border: none;
      font-family: 'Muli', serif;
      font-size: 2rem;
      text-transform: uppercase;
      color: #fff;
      text-shadow: 0 .2rem .4rem rgba(0,0,0,0.2);
      box-shadow: 0 .3rem .5rem rgba(0,0,0,0.4);
      cursor: pointer;
      animation: moveBanner 1s .9s forwards;
    }
  }

  @keyframes moveBanner {
    0% {
      transform: translateY(40rem) rotateY(-20deg);
    }
    100% {
      transform: translateY(0) rotateY(0);
      opacity: 1;
    }
  }
} /*  header */

.sidebar {
  width: 40rem;
  height: 100vh;
  position: fixed;
  top: 0;
  // right: 0;
  right: -40rem;
  background-color: #fff;
  transition: right .5s;
  z-index: 100;

  .menu {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);

    .menu-item {
      text-align: center;

      .menu-link {
        font-family: 'Baloo Da 2', serif;
        font-size: 4rem;
        color: #555;
        position: relative;

        &::before {
          content: attr(data-content);
          position: absolute;
          top: 0;
          left: 0;
          color: #c29525;
          width: 0;
          overflow: hidden;
          white-space: nowrap;
        }

        &:hover::before {
          width: 100%;
          transition: width .3s ease-in-out;
        }
      } /*  menu-link */
    } /*  menu-item */
  } /*  menu */

  .social-media {
    position: absolute;
    bottom: 3rem;
    width: 100%;
    display: flex;
    justify-content: center;

     .social-media-link {
      font-size: 2.2rem;
      margin: 3rem;
      width: 4.5rem;
      height: 4.5rem; 
      background-color: #777;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      transition: background-color 0.3s;

      &:hover {
        background-color: #c29525;
      }

      .svg-inline--fa {
        font-size: 2.2rem;
        color: #fff;
      }
    }
  } /*  social-media */

} /*  sidebar */

.change .sidebar {
  right: 0;
}

.section-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 7rem 0 10rem 0;

  .section-heading {
    font-family: "Muli", serif;
    font-size: 5rem;
    font-weight: 300;
    color: #4b4b4b;
    margin-bottom: 6rem;
  }

  .underline {
    width: 12rem;
    height: .3rem;
    background-color: #c29525;
  }
} /*  section-header */

 
/* ====== About us ====== */
.about-us {
  width: 100%;
  background-color: #f5f5f5;
  padding-bottom: 15rem;

  .services {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(16,1fr);
    grid-template-rows: repeat(6,6rem);
    grid-gap: 4rem;

    .service {
      width: 100%;
      margin-bottom: 2rem;

      .service-header {
        display: flex;
        align-items: center;
        margin-bottom: 1rem;

        svg {
          font-size: 4rem;
          color: #4b4b4b;
          margin-right: 2rem;
        }

        h3 {
          font-family: "Baloo Da 2", serif;
          font-size: 2.6rem;
          line-height: 2.6rem;
          font-weight: 400;
          margin-bottom: 2rem;
        }
      }

      .service-text {
        font-family: "Josefin Slab", serif;
        font-size: 1.6rem;
      }


      &:nth-child(1) {
        grid-column: 4/7;
        grid-row: 1/3;
      }

      &:nth-child(2) {
        grid-column: 3/6;
        grid-row: 3/5;
      }

      &:nth-child(3) {
        grid-column: 4/7;
        grid-row: 5/-1;
      }

      &:nth-child(4) {
        grid-column: 11/14;
        grid-row: 1/3;
      }

      &:nth-child(5) {
        grid-column: 12/15;
        grid-row: 3/5;
      }

      &:nth-child(6) {
        grid-column: 11/14;
        grid-row: 5/-1;
      }

    } /*  service */

    .about-us-img-wrapper {
      grid-column: 7 / 11;
      grid-row: 2 / 6;
      width: 100%;

      img {
        width: 100%;
        object-fit: cover;
        opacity: .8;
      }
    }

  } /*  services */
} /*  about-us */

 
/* ====== Team ====== */
.team {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 5rem 20rem 5rem;

  .cards-wrapper {
    display: flex;
    justify-content: space-evenly;
    margin-top: 8rem;
    width: 100%;

    .card {
      width: 37rem;
      height: 45rem;
      box-shadow: 0 1rem 4rem rgba(0,0,0,0.4);
      border-radius: .5rem;
      position: relative;

      &:hover .card-img-wrapper img {
        opacity: .5;
      }

      &:hover .card-info {
        bottom: 2rem;
        opacity: 1;
        visibility: visible;
        transition: all .3s;
      }

      .card-img-wrapper {
        width: 100%;
        height: 100%;
        background-color: #262626;
        border-radius: .5rem;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          opacity: .8;
          border-radius: .5rem;
          transition: opacity .3s;
        }
      }

      .card-info {
        position: absolute;
        bottom: 0;
        padding: 2rem;
        text-shadow: 0 .2rem .5rem rgba(0,0,0,0.4);
        opacity: 0;
        visibility: hidden;

        h2 {
          font-family: "Baloo Da 2", serif;
          font-size: 2.5rem;
          line-height: 2.5rem;
          font-weight: 300;
          color: #eee;
        }

        h3 {
          font-family: "Muli", serif;
          font-size: 2rem;
          font-weight: 500;
          color: #a52a2a;
          margin-bottom: 1rem;
        }

        p {
          font-family: "Baloo Da 2", serif;
          font-size: 1.4rem;
          line-height: 1.6rem;
          font-weight: 300;
          color: #eee;
          width: 80%;
          margin-bottom: 2rem;
        }

        button {
          width: 10rem;
          height: 3rem;
          background-color: #c29525;
          border: none;
          font-family: "Baloo Da 2", serif;
          font-size: 1.4rem;
          line-height: 1.5rem;
          color: #eee;
          border-radius: .3rem;
          box-shadow: 0 .1rem .8rem rgba(0,0,0,0.4);
        }
      } /*  card-info */
    } /*  card */
  } /*  cards-wrapper */
} /*  team */


 
/* ====== Contact ====== */
.contact {
  width: 100%;
  height: 100vh;
  background-color: #272727;
  display: flex;
  justify-content: center;
  align-items: center;

  .contact-wrapper {
    width: 60%;
    height: 75rem;
    display: flex;
    // background-color: #fff;
    box-shadow: 0 3rem 7rem rgba(0,0,0,0.5);

    .contact-left {
      width: 35%;
      background: linear-gradient(rgba(15,15,15,.6),rgba(22,22,22,.9)), url("../images/contact-bg.jpg")center no-repeat;
      background-size: cover;
    }

    .contact-right {
      width: 65%;
      background-color: #eee;
      padding: 3rem 10rem 10rem 10rem;

      .contact-heading {
        font-family: 'Baloo Da 2', serif;
        font-size: 6rem;
        font-weight: 300;
        color: #272727;
        margin-bottom: 5rem;
        text-align: center;
      }

      form {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        .field {
          width: 45rem;
          background-color: transparent;
          border: none;
          border-bottom: .2rem dashed #636363;
          margin: 3rem 0;
          padding: 1rem 1rem 1rem 0;
          font-family: "Muli", serif;
          font-size: 1.6rem;
          color: #4b4b4b;

          &:focus {
            border-bottom-style: solid;
          }

          &:focus ~ label {
            transform: translateY(-3rem);
            font-size: 1.2rem;
          }
        }

        .input-group {
          position: relative;

          input{
            height: 4rem;
          }
          textarea {
            max-height: 7rem;
            max-width: 45rem;
          }
          label {
            position: absolute;
            left: 0;
            font-family: "Baloo Da 2", serif;
            font-size: 1.8rem;
            color: #4b4b4b;
            text-transform: uppercase;
            pointer-events: none;
            transition: all 0.3s;
          }
          .input-label {
            bottom: 3rem;
          }
          .message {
            bottom: 6rem;
          }
        }

        .submit-btn {
          width: 45rem;
          height: 5rem;
          background-color: #c29525;
          color: #fff;
          border: none;
          margin-top: 2rem;
          font-family: 'Muli', serif;
          font-size: 2rem;
          font-weight: 300;
          text-transform: uppercase;
          letter-spacing: .2rem;
          cursor: pointer;
          text-shadow: 0 .1rem .3rem rgba(0, 0, 0, 0.5);
          box-shadow: 0 .1rem .3rem rgba(0, 0, 0, 0.3);
        }
      } /*  form */
    } /*  contact-right */
  } /*  contact-wrapper */
} /*  contact */

 
/* ====== Footer ====== */
.footer {
  width: 100%;
  height: 15rem;
  background-color: #17181b;
  display: flex;
  justify-content: center;
  align-items: center;

  .footer-content {
    width: 60%;
    display: flex;
    justify-content: space-between;

    .copyright {
      font-family: "Baloo Da 2",serif;
      font-size: 1.6rem;
      color: #a7a7a7;
    }

    .social-list {
      a {
        margin: 0 2rem;
      }
      svg {
        font-size: 2rem;
        color: #a7a7a7;
      }
    }

  } /* content */
} /* footer */
 
 
 
/* ====== Scroll button at the bottom page ====== */
.scroll-btn {
  position: fixed;
  right: 5rem;
  bottom: 5rem;
  width: 4.5rem;
  height: 4.5rem;
  background-color: #e2b646;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;;
  color: #fff;
  box-shadow: 0 0.1rem 0.6rem rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
}
 

 
/* ====== Responsive ====== */

@media (max-width: 1500px) {
  .about-us-img-wrapper {
    grid-row: 3 / -1;
  }

  .card {
    width: 34rem;
  }

  .contact-wrapper {
    width: 80%;
    height: 65rem;
  }

  .footer-content {
    width: 80%;
  }
}

@media (max-width: 1400px) {
  .banner h1 {
    font-size: 6rem;
    line-height: 7rem;
  }

  .banner p {
    font-size: 3rem;
  }

  .banner button {
    width: 20rem;
    height: 5rem;
    font-size: 1.6rem;
  }

  .menu-link {
    font-size: 3rem;
  }

  .service:nth-child(1) {
    grid-column: 3 / 7;
  }

  .service:nth-child(2) {
    grid-column: 2 / 6;
  }

  .service:nth-child(3) {
    grid-column: 3 / 7;
  }

  .service:nth-child(4) {
    grid-column: 11 / 15;
  }

  .service:nth-child(5) {
    grid-column: 12 / 16;
  }

  .service:nth-child(6) {
    grid-column: 11 / 15;
  }
}

@media (max-width: 1300px) {
  .team {
    padding-bottom: 5rem;
  }

  .cards-wrapper {
    flex-direction: column;
    align-items: center;
    margin-top: 4rem;
  }

  .card {
    margin-bottom: 8rem;
  }

  .contact-wrapper {
    width: 90%;
    height: 55rem;
  }

  .contact-heading {
    margin-bottom: 2rem;
  }

  .field {
    margin: 2rem 0;
  }
}

@media (max-width: 1000px) {
  .banner h1 {
    font-size: 5rem;
    line-height: 6rem;
  }

  .banner p {
    font-size: 2.5rem;
  }

  .banner button {
    width: 18rem;
    height: 4rem;
    font-size: 1.5rem;
  }

  .services {
    display: flex !important;
    flex-direction: column;
    align-items: center;
  }

  .service {
    width: 40rem !important;
    margin-bottom: 6rem;
  }

  .about-us-img-wrapper {
    width: 40rem !important;
  }

  .about-us-img-wrapper img {
    width: 100%;
  }

  .contact-left {
    width: 0;
  }

  .contact-right {
    width: 100%;
  }

  .field {
    width: 55rem;
  }

  .input-group textarea {
    max-width: 55rem;
  }

  .submit-btn {
    width: 55rem;
  }

  .footer-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 50%;
  }

  .copyright {
    order: 1;
    margin-top: 3rem;
  }
}

@media (max-width: 700px) {
  .banner h1 {
    font-size: 4rem;
    line-height: 5rem;
  }

  .banner p {
    font-size: 2rem;
  }

  .field {
    width: 35rem;
  }

  .input-group textarea {
    max-width: 35rem;
  }

  .submit-btn {
    width: 35rem;
  }
  .contact .contact-wrapper {
    width: 100%;
  }

  .contact-right {
    width: 100% !important;
  }

  .contact-left {
    display: none;
  }
}

@media (max-width: 500px) {
  html {
    font-size: 45%;
  }

  .sidebar {
    width: 100%;
    right: -100%;
  }

  .change .hamburger-menu {
    right: 38rem;
  }

  .service {
    width: 30rem;
  }

  .footer {
    height: 18rem;
  }

  .contact .contact-wrapper {
    width: 100%;
  }
  .contact-right {
    width: 100% !important;
  }

  .contact-left {
    display: none;
  }
}

 
 
/* ====== Loader spinner ====== */
.display .spinner-container {
  opacity: 0;
  visibility: hidden;
}

.container {
  display: none;
}

.display .container {
  display: block;
}
 
.spinner-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #262626;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  z-index: 300;

  .circles {
    width: 8rem;
    height: 8rem;
    // background-color: #fff;
    position: relative;
    opacity: 0;
    visibility: hidden;
    animation: displayCircles 4s; 

    div {

      animation:circles 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
      transform-origin: 4rem 4rem;

      &:after {
        content: "";
        position: absolute;
        width: .7rem;
        height: .7rem;
        border-radius: 50%;
        background-color: #c29525;
        margin: -0.4rem 0 0 -0.4rem;
      }

      &:nth-child(1) {
        animation-delay: -0.036s;
         &::after {
          top: 6.3rem;
          left: 6.3rem;
        }
      }

      &:nth-child(2) {
        animation-delay: -0.072s;
        &::after {
          top: 6.8rem;
          left: 5.6rem;
        }
      }

      &:nth-child(3) {
        animation-delay: -0.108s;
        &::after {
          top: 7.1rem;
          left: 4.8rem;
        }
      }

      &:nth-child(4) {
        animation-delay: -0.144s;
        &::after {
          top: 7.2rem;
          left: 4rem;
        }
      }

      &:nth-child(5) {
        animation-delay: -0.18s;
         &::after {
          top: 7.1rem;
          left: 3.2rem;
        }
      }
      
      &:nth-child(6) {
        animation-delay: -0.216s;
        &::after {
          top: 6.8rem;
          left: 2.4rem;
        }
      }

      &:nth-child(7) {
        animation-delay: -0.252s;
        &::after {
          top: 6.3rem;
          left: 1.7rem;
        }
      }

      &:nth-child(8) {
        animation-delay: -0.288s;
        &::after {
          top: 5.6rem;
          left: 1.2rem;
        }
      }

      // &:nth-child(1)::after {
      //   top: 6.3rem;
      //   left: 6.3rem;
      // }

      // &:nth-child(2)::after {
      //   top: 6.8rem;
      //   left: 5.6rem;
      // }

      //  &:nth-child(3)::after {
      //   top: 7.1rem;
      //   left: 4.8rem;
      // }

      //  &:nth-child(4)::after {
      //   top: 7.2rem;
      //   left: 4rem;
      // }

      //  &:nth-child(5)::after {
      //   top: 7.1rem;
      //   left: 3.2rem;
      // }

      //  &:nth-child(6)::after {
      //   top: 6.8rem;
      //   left: 2.4rem;
      // }

      //  &:nth-child(7)::after {
      //   top: 6.3rem;
      //   left: 1.7rem;
      // }

      //  &:nth-child(8)::after {
      //   top: 5.6rem;
      //   left: 1.2rem;
      // }
    } /* div */

     
  } /*  circles */
} /*  spinner-container */

@keyframes displayCircles {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  25% {
    opacity: 1;
    visibility: visible;
  }
  90% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes circles {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(560deg);
  }
}